.Wrapper {
	position: relative;
	margin-top: 20px;
}

.Search {
	width: 100%;
	box-sizing: border-box;
	padding: 10px 0 10px 35px;
	border-radius: 50px;
	border: none;

	&:focus {
		outline: none;
	}
}

.Label {
	position: absolute;
	left: 10px;
	top: 9px;
	z-index: 10;
	color: #777;
}
