.Heading {
	margin: 0;
	padding: 30px 0 30px 20px;
	font-size: 30px;
	color: rgb(0, 162, 255);
	font-weight: 400;
}

.ChatList {
	padding: 0;
	margin: 0;
	display: flex;
	flex-direction: column;
	flex-grow: 1;

	li {
		list-style: none;
		padding: 0;
		margin: 0;
	}
}

.Wrapper {
	border-right: 1px solid #ddd;
	flex-grow: 1;
	overflow: scroll;
	overflow-x: hidden;
}
